import React, { Component } from 'react';
import Head from './components/head'
import Weather from './components/weather'

const API_KEY = "6e0ce2c9a22ee0e61b0e018e28b815b6";

class App extends Component {

  state = {
    position: {
      lat: undefined,
      lon: undefined,
      name: undefined,
    }, 
    temp: {
      fahr: undefined, 
      cel: undefined,
      humidity: undefined,
      wind: undefined,
      summary: undefined,
      subSummary: undefined,
      icon: 'CLEAR_DAY',
    }, 
  }

  getWeather = async (geo) => {

    const proxy = 'https://cors-anywhere.herokuapp.com/';
    const apiCall = await fetch(`${proxy}https://api.darksky.net/forecast/${API_KEY}/${this.state.position.lat},${this.state.position.lon}`);
    const data = await apiCall.json();

    this.setState((prevState) => ({
      position: {
        ...prevState.position,
        name: geo ? data.timezone : this.state.position.name,
      },
      temp: {
        fahr: Math.round(data.currently.temperature),
        cel: Math.round((data.currently.temperature - 32) * 5/9),
        humidity: data.currently.humidity,
        wind: data.currently.windSpeed,
        summary: data.currently.summary, 
        subSummary: data.hourly.summary,
        icon: data.currently.icon.replace(/-/g,"_").toUpperCase(),
      }
    }));
  }

  getGeoPos = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
          pos => {
            this.setState((prevState) => ({
              position: {
                ...prevState.position,
                lat: pos.coords.latitude, 
                lon: pos.coords.longitude,
              },
              temp: {
                ...prevState.temp,
              }
            }));
            this.getWeather(true); 
          }
      );
    } else { 
        alert("Geolocation is not supported by this browser.");
    }
  }

  componentDidMount() {
    this.getGeoPos();
  }


  render() {
    return (
      <div className="App">
        <Head 
          position={this.state.position}
          temp={this.state.temp}
          getWeather={this.getWeather}
          getGeoPos={this.getGeoPos}
        />
        <Weather 
          position={this.state.position}
          temp={this.state.temp}
          getWeather={this.getWeather}
        /> 
      </div>
    );
  }
}

export default App;
